import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import Header from './components/header'
import Main from './components/main'
import './assests/styles/main.css';

class App extends Component {
  constructor(props) {
		super(props);
		this.state = {};
	}
  handleClick(){
    let shadesEl = document.querySelector('.body');
    if (shadesEl.classList.contains('open')) {
      shadesEl.classList.remove('open');
     } else {
      shadesEl.classList.add('open');
     }
   }
  render() {
    return (
      <div className="self_layout_second" style={{height:"100vh"}}>
          <a href="javascript:void(0)" className="mobileMenu" onClick={this.handleClick.bind(this)}><FontAwesome name={"bars"}/></a>
          <Header/>
          <Main/>
      </div>
    );
  }
}

export default App;



