import React, { Component } from 'react';
import Slider from "react-slick";
import '.././assests/styles/slick.css';
import jsonData from '.././data/projects.json';
const settings = {
      dots: false,
      infinite: true,
      speed: 500,
      arrows: false,
      slidesToShow: 1,
      slidesToScroll: 1
    };
class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {homeData: {}};
  }
  componentWillMount(){
    let homeDetail = jsonData.myData.filter(person => person.pageName === "home");
    this.setState({homeData:homeDetail});

  }
  componentDidMount() {
    window.addEventListener('wheel', this.slide.bind(this));
  }

  componentWillUnmount() {
      window.removeEventListener('wheel', this.slide.bind(this));
  }

  slide(y){
    if(this.slider !== null){
      y > 0 ? (
         this.slider.slickNext()
      ) : (
         this.slider.slickPrev()
      )
    }
  }
  render() {
    let homeData = this.state.homeData;
    return (
      <div className="self_page_sections">
          <Slider {...settings} ref={slider => this.slider = slider }>
            {
              homeData[0].Content.map((list,i)=>{
                let newUrl = require('.././assests/images/'+ list.imagepath);
                return (
                  <div key ={i} style={{height:'100%'}}>
                      <div className="prt_home_wrapper" style={{background:'url('+  newUrl +')'}}>
                      <div className="displayTable">
                          <div className="verticalCenter">
                            <div className="container">
                              <div className="row">
                                <div className="col-sm-6 col-sm-offset-6">
                                  <h1 className="homeTitle">{list.description}</h1>
                                </div>
                              </div>
                            </div>
                          </div>
                      </div>
                      <div className="overlay"></div>
                    </div>
                  </div>
                )
              })
            }
          </Slider>
      </div>
    );
  }
}

export default Home;



