import React, { Component } from 'react';
import { Link} from 'react-router-dom';
class WorkList extends Component {
  render() {
    let data = this.props.data;
    return (
      <div className="row">
        <ul className="prt_portfolio_box">
            {data.map((di, i) => {
              return <li key={i} className="col-sm-4">
                  <Link to={`/portfolio/${di.id}`}>
                    <div className="prt_portfolio_img">
                      <img src={require('.././assests/images/'+di.imagePath)} alt ="im-title"/>
                      <div className="prt_portfolio_text">
                        <h4>{di.title}</h4>
                        <p>{di.technology.reduce((array , currentValue , i) => {return array  + currentValue }, "")}</p>
                      </div>
                    </div>
                  </Link>
              </li>;
            })}
        </ul>
    </div>
    );
  }
}

export default WorkList;



