import React from 'react';
import { NavLink , Link} from 'react-router-dom';
import logo from ".././assests/images/logo.jpg";
import Footer from './footer'

const Header = () => (
  <header className="self_header">
      <div className="self_logo">
  		    <Link to='/'> <img src={logo} alt="pushkar logo"/></Link>
      </div>
      <nav className="self_layout_nav">
          <ul>
              <li><NavLink to='/' exact={true} activeClassName="active" >Home</NavLink></li>
              <li><NavLink to='/aboutMe' activeClassName="active">About Me</NavLink></li>
              <li><NavLink to='/portfolio' activeClassName="active">Portfolio</NavLink></li>
              <li><NavLink to ='/contactme' activeClassName="active">contact Me</NavLink></li>
          </ul>
      </nav>
      <div className="self-resume-download">
          <a href="" className="prt_btn">
              <i className="shape-one"></i>
              <i className="shape-two"></i>resume
              <i className="shape-one shape-three"></i>
              <i className="shape-two shape-four"></i>
          </a>
      </div>
      <Footer/>
  </header>
)

export default Header



