import React, { Component } from 'react';
import Title from '.././components/titleComponent';
import EducationList from '.././components/educationList';
import jsonData from '.././data/projects.json';


class About extends Component {
  constructor(props) {
    super(props);
    this.state = {aboutData: {}};
    this.setAboutData = this.setAboutData.bind(this);
  }
  componentWillMount(){
    let aboutDetail = jsonData.myData.filter(person => person.pageName === "About");
    this.setAboutData(aboutDetail);
  }
  setAboutData(data){
    if(data && data !== undefined){
      let newData = {};
      data.map((ct,r) => {
          let content = ct.Content;
          content.map((cntnt,r) => {
            newData= cntnt;
            return newData;
          });
        return  newData;
      });
      this.setState({aboutData:newData})
    }
  }
  render() {
    let aboutData = this.state.aboutData;
    return (
      <div className="self_page_sections">
        <div className="about_wrapper toppadder115">
            <div className="container">
              <div className="row">
                <div className="prt_about_info">
                  <div className="col-sm-6">
                    <figure>
                      <img src={require('.././assests/images/'+aboutData.imagepath)} alt="aboutImg"/>
                    </figure>
                  </div>
                  <div className="col-sm-6">
                    <Title title={aboutData.title} subtitle ={aboutData.subtitle}/>
                    <div className="prt_about_details">
                      <p><strong>{aboutData.aboutTitle}</strong></p>
                      <p>{aboutData.description}</p>
                    </div>
                  </div>
                </div>
                <div className="prt_about_edulearn_wrapper prt_bottompadder115">
                  <div className="col-sm-12">
                    <Title title={aboutData.education[0].title} subtitle ={aboutData.education[0].subtitle}/>
                    <div className="prt_about_learnsection">
                      <EducationList listData={aboutData.education[0].educationDetails} classIS={"education"} isTrue={false}/>
                    </div>
                  </div>
                </div>
                <div className="prt_about_experience_wrapper">
                  <div className="col-sm-12">
                    <Title title={aboutData.experience[0].title} subtitle ={aboutData.experience[0].subtitle}/>
                    <div className="prt_about_experience">
                      <EducationList listData={aboutData.experience[0].experienceDetails} classIS={"experience"} isTrue={true}/>
                    </div>
                  </div>
                </div>
                <div className="prt_skills_wrapper">
                  <div className="col-sm-12">
                    <Title title={aboutData.Strength[0].title} subtitle ={aboutData.Strength[0].subtitle}/>
                    <p className="skillsP">{aboutData.Strength[0].description} </p>
                    <ul className="imagesList">
                      {aboutData.Strength[0].skills.map(function(skills, i){
                        return <li key={i}>
                          <div className={["pie-wrapper progress-",skills.count].join('')}>
                            <span className="label">{skills.count}<i className="smaller">%</i></span>
                            <div className="pie">
                              <div className="left-side half-circle" style={{borderColor: skills.color}}></div>
                              <div className="right-side half-circle" style={{borderColor: skills.color}}></div>
                            </div>
                          </div>
                          <h3>{skills.title}</h3>
                        </li>
                      })}
                    </ul>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
    );
  }
}

export default About;



