import React, { Component } from 'react';

class EducationList extends Component {
  render() {
    let listData = this.props.listData;
    let isClass = this.props.classIS;
    let isTrue= this.props.isTrue;
    return (
      <ul className={[isClass, "list"].join(' ')}>
          {listData.map(function(listValue, i){
            return <li key={i}>
              <div className="info_box">
                <div className="year">
                  <h3>{isTrue ? listValue.toTyear : listValue.year}</h3>
                  {isTrue ? <span>{listValue.fromTyear}</span> :null}
                </div>
                <div className="info">
                  <h4>{isTrue ? listValue.position : listValue.qualificiation}</h4>
                  <span>{listValue.loaction}</span>
                  <p>{listValue.description}</p>
                </div>
              </div>
            </li>;
          })}
      </ul>
    );
  }
}

export default EducationList;



