import React, { Component } from 'react';

class Title extends Component {
  render() {
    return (
      <div className="prt_heading_wrapper">
        <div className="prt_heading ">
          <h1>{this.props.title}</h1>
          <p>{this.props.subtitle}</p>
        </div>
      </div>
    );
  }
}

export default Title;



