import React from 'react';
import { Switch, Route } from 'react-router-dom';
import Home from '../screens/home';
import About from '../screens/about';
import Work from '../screens/work';
import PortfolioWork from '../screens/portfolioWork';
import ContactMe from '../screens/contact';
import NotFound from '../screens/notfound';

const Main = () => (
  <main className="self_section_body">
    <div className="self_home_page">
        <div className="self_pages">
            <Switch>
              <Route exact path='/' component={Home}/>
              <Route path='/aboutMe' component={About}/>
              <Route exact path='/portfolio' component={Work}/>
              <Route path='/portfolio/:portId' component={PortfolioWork} />
              <Route exact path='/contactme' component={ContactMe}/>
              <Route path="*" component={NotFound} />
            </Switch>
        </div>
    </div>
  </main>
)

export default Main



