import React, { Component } from 'react';
import Title from '.././components/titleComponent';
import WorkList from '.././components/workList';
import jsonData from '.././data/projects.json';
class Work extends Component {
  constructor(props) {
    super(props);
    this.state = {workData: {}};
    this.setWorkData = this.setWorkData.bind(this);
  }
  componentWillMount(){
    let aboutDetail = jsonData.myData.filter(person => person.pageName === "Portfolio");
    this.setWorkData(aboutDetail);
  }
  setWorkData(data){
    if(data && data !== undefined){
      let newData = {};
      data.map((ct,r) => {
          let content = ct.Content;
          content.map((cntnt,r) => {
            newData= cntnt;
            return newData;
          });
        return  newData;
      });
      this.setState({workData:newData})
    }
  }
  render() {
    let workData = this.state.workData;
    return (
      <div className="self_page_sections">
        <div className="prt_portfolio_wrapper prt_toppadder115">
          <div className="container-fluid">
            <div className="row">
                <div className="col-xs-12 text-center">
                  <Title title={workData.title} subtitle ={workData.subtitle}/>
                  <div className="workDescription">
                    <p> {workData.description}</p>
                  </div>
                  <WorkList data={workData.portfolioWork}/>
                </div>
            </div>
          </div>
        </div>
    </div>
    );
  }
}

export default Work;



