import React, { Component } from 'react';
import FontAwesome from 'react-fontawesome';
import Title from '.././components/titleComponent';
import jsonData from '.././data/projects.json';

class ContactMe extends Component {
  constructor(props) {
    super(props);
    this.state = {contactData: {}};
  }
  componentWillMount(){
    let conatactDetail = jsonData.myData.filter(person => person.pageName === "ContactMe");
    this.setState({contactData:conatactDetail[0].Content[0]})
  }

  render() {
    let contactData = this.state.contactData;
    let newUrl = require('.././assests/images/'+ contactData.imagepath);
    return (
      <div className="self_page_sections">
        <div className="Contact_wrapper">
            <div className="imageConatiner" style={{background:'url('+  newUrl +')'}}>
                <span className="overlay"></span>
            </div>
            <div className="textContainer">
              <Title title={contactData.title} subtitle ={contactData.subtitle}/>
              <iframe src={contactData.mapLoaction} title="contact" allowfullscreen></iframe>
              <div className="conatactDetail">
                <h3>Address</h3>
                <ul>
                  <li><FontAwesome name="map-marker"/>{contactData.address} </li>
                </ul>
                <h3>contact</h3>
                <ul>{
                  contactData.contactDetail.map((m,i) => {
                    return <li key = {i}><FontAwesome name={m.icon}/>{m.text}</li>
                  })}
                </ul>
                <h3>Follow me</h3>
                <ul className="links">
                  {contactData.followMe.map((m, i) => {return <li key={i}><a href={m.url}><FontAwesome name={m.icon}/></a></li>})}
                </ul>
              </div>
            </div>
        </div>
      </div>
    );
  }
}

export default ContactMe;



