import React, { Component } from 'react';
import { Link} from 'react-router-dom';
import Title from '.././components/titleComponent';
import jsonData from '.././data/projects.json';
class PortfolioWork extends Component {
  constructor(props) {
    super(props);
    this.setPortfolioData = this.setPortfolioData.bind(this);
  }
  componentWillMount(){
    let prmUrl = this.props.match.params;
    let filterData = jsonData.myData.filter(person => person.pageName === "Portfolio");
    let filterItems = filterData[0].Content[0].portfolioWork;
    let exactMatch =  filterItems.filter(portFolioId => portFolioId.id === prmUrl.portId);
    this.setPortfolioData(exactMatch);
  }
  setPortfolioData(data){
    if(data && data !== undefined){
      let newData = {};
      data.map((ct,r) => {
        newData = ct;
        return  newData;
      });
      this.setState({portData:newData})
    }
  }
  render() {
    let stateData = this.state.portData;
    return (
      <div className="self_page_sections project-section">
        <Link to='/portfolio' className="backToPage"> <i className="glyphicon glyphicon-hand-left"></i> back to portfolio</Link>
        <div className="container-fluid">
          <Title title={"PORTFOLIO DETAILS"} subtitle ={"MY WORK"}/>
            <div className="row top40">
              <div className="col-sm-12">
                <figure className="project-image">
                  <img src={require('.././assests/images/'+stateData.imagePath)} alt="portfolioImg"/>
                </figure>
              </div>
              <div className="col-sm-12">
                <div className="project-text">
                    <h1>{stateData.title}</h1>
                    <p className="subTitle"><strong>{stateData.subTitle}</strong></p>
                    <p>{stateData.description}</p>
                    <div className="technology_used">
                      <strong>TECHNOLOGIES USED</strong>
                      <p>
                        {stateData.technology.map((array , i) => {return <span key={i}>{array} </span> })}
                      </p>
                    </div>
                </div>
              </div>
            </div>
        </div>
      </div>
    );
  }
}

export default PortfolioWork;



